@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*:before,
*:after {
  box-sizing: border-box; }

html,
body,
div,
span,
object,
iframe,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
code,
em,
img,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
ol,
ul,
li,
fieldset,
form,
label,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
main,
canvas,
embed,
footer,
header,
nav,
section,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
      -ms-text-size-adjust: none;
          text-size-adjust: none; }

footer,
header,
nav,
section,
main {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

input {
  -webkit-appearance: none;
  border-radius: 0; }

body {
  font-family: 'Poppins', sans-serif;
  font-size: 14px; }

body {
  width: 100vw; }

.App {
  width: 100vw;
  overflow-x: hidden;
  margin: 0 auto; }

.footer {
  background-color: #000;
  color: #fff;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0; }

.box {
  width: 1440px;
  max-width: 60%;
  padding: 60px 0px;
  margin: 0 auto; }
  .box .wrapper {
    display: flex;
    flex-direction: column; }
    @media (max-width: 1024px) {
      .box .wrapper {
        margin-bottom: 20px; } }
  .box__logo {
    font-size: 18px;
    font-weight: 800; }
  .box__socialmedia a {
    font-size: 18px;
    color: #fff;
    margin-right: 10px; }
  .box span {
    margin-top: 10px; }

.box--container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-weight: 500; }

.hero {
  height: 65vh;
  border: 25px solid #000;
  background-size: cover;
  background-position: center top;
  width: 95%;
  max-width: 100%;
  margin: 0 auto;
  margin-bottom: 50px; }

header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999; }
  header .inner-header {
    position: relative;
    height: 65px;
    width: 95%;
    max-width: 95%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1001; }
    header .inner-header .logo a {
      font-weight: 700;
      font-size: 1rem;
      text-decoration: none;
      color: #000000; }
    header .inner-header .menu button {
      border: none;
      background: none;
      outline: none;
      cursor: pointer;
      font-size: 0.8rem;
      mix-blend-mode: difference; }
  header .hamburger-menu {
    display: none;
    top: 0;
    left: 50%;
    right: 0;
    position: fixed;
    width: 100%;
    height: 100%; }
    @media (max-width: 1024px) {
      header .hamburger-menu {
        left: 25%; } }
    @media (max-width: 768px) {
      header .hamburger-menu {
        left: 0; } }
    @media (max-width: 480px) {
      header .hamburger-menu {
        left: 0; } }
    header .hamburger-menu .menu-secondary-background-color {
      opacity: 0.5;
